/**
 * Integration layer for API Optimization Manager
 * 
 * This module provides wrapper functions and adapters to integrate the optimization
 * manager with existing API clients, stores, and data fetching patterns across
 * all workout application versions.
 */

import ApiOptimizationManager from './ApiOptimizationManager';

// Type imports for consistency
interface Exercise {
  muscleGroup: string;
  exercise: string;
  difficultyLevel?: string;
  equipmentRequired?: string;
  targetIntensity?: string;
  primaryMuscleGroup?: string;
  secondaryMuscleGroup?: string;
  exerciseDuration?: string;
  recoveryTime?: string;
  exerciseType?: string;
  caloriesBurned?: number;
  exerciseProgression?: string;
  injuryRiskLevel?: string;
  exerciseLink?: string;
  imageLink?: string;
  relatedPath?: string;
}

interface WorkoutTemplate {
  id?: string;
  name: string;
  description?: string;
  exercises: any[];
  createdAt: string;
  lastUsed?: string;
}

interface WorkoutLogEntry {
  id?: string;
  date: string;
  muscleGroup: string;
  exercise: string;
  reps: number;
  weight: number;
  rating: number;
  restTime?: number;
}

interface TodaysWorkout {
  date: string;
  workoutData: any;
  completed: boolean;
  notes?: string;
}

class ApiIntegrationLayer {
  private static instance: ApiIntegrationLayer;
  private optimizationManager: ApiOptimizationManager;
  private originalApiFunctions: Map<string, Function> = new Map();

  private constructor() {
    this.optimizationManager = ApiOptimizationManager.getInstance();
    this.initializeIntegration();
  }

  public static getInstance(): ApiIntegrationLayer {
    if (!ApiIntegrationLayer.instance) {
      ApiIntegrationLayer.instance = new ApiIntegrationLayer();
    }
    return ApiIntegrationLayer.instance;
  }

  /**
   * Initialize integration with existing API functions
   */
  private initializeIntegration(): void {
    // Store references to original functions for fallback
    this.storeOriginalFunctions();
    
    // Set up navigation tracking
    this.setupNavigationTracking();
  }

  /**
   * Optimized wrapper for exercise data fetching
   */
  public async fetchExercises(
    originalFetcher: () => Promise<Exercise[]>,
    options: {
      forceRefresh?: boolean;
      muscleGroup?: string;
      exercise?: string;
    } = {}
  ): Promise<Exercise[]> {
    const { muscleGroup, exercise, ...optimizationOptions } = options;
    
    return this.optimizationManager.optimizedFetch(
      originalFetcher,
      'exercises',
      { muscleGroup, exercise },
      {
        ...optimizationOptions,
        dependencies: ['muscleGroups']
      }
    );
  }

  /**
   * Optimized wrapper for muscle groups fetching
   */
  public async fetchMuscleGroups(
    originalFetcher: () => Promise<string[]>,
    options: { forceRefresh?: boolean } = {}
  ): Promise<string[]> {
    return this.optimizationManager.optimizedFetch(
      originalFetcher,
      'muscleGroups',
      {},
      options
    );
  }

  /**
   * Optimized wrapper for workout templates
   */
  public async fetchWorkoutTemplates(
    originalFetcher: () => Promise<WorkoutTemplate[]>,
    options: { forceRefresh?: boolean } = {}
  ): Promise<WorkoutTemplate[]> {
    return this.optimizationManager.optimizedFetch(
      originalFetcher,
      'workoutTemplates',
      {},
      {
        ...options,
        dependencies: ['exercises']
      }
    );
  }

  /**
   * Optimized wrapper for workout logs
   */
  public async fetchWorkoutLogs(
    originalFetcher: () => Promise<WorkoutLogEntry[]>,
    options: {
      forceRefresh?: boolean;
      dateRange?: { start: string; end: string };
      muscleGroup?: string;
      exercise?: string;
    } = {}
  ): Promise<WorkoutLogEntry[]> {
    const { dateRange, muscleGroup, exercise, ...optimizationOptions } = options;
    
    return this.optimizationManager.optimizedFetch(
      originalFetcher,
      'workoutLogs',
      { dateRange, muscleGroup, exercise },
      {
        ...optimizationOptions,
        dependencies: ['dashboardStats']
      }
    );
  }

  /**
   * Optimized wrapper for today's workout
   */
  public async fetchTodaysWorkout(
    originalFetcher: () => Promise<TodaysWorkout>,
    options: { forceRefresh?: boolean } = {}
  ): Promise<TodaysWorkout> {
    return this.optimizationManager.optimizedFetch(
      originalFetcher,
      'todaysWorkout',
      {},
      {
        ...options,
        priority: 'high' // Today's workout is high priority
      }
    );
  }

  /**
   * Batch fetch multiple data types commonly used together
   */
  public async batchFetchDashboardData(
    fetchers: {
      exercises: () => Promise<Exercise[]>;
      workoutLogs: () => Promise<WorkoutLogEntry[]>;
      todaysWorkout: () => Promise<TodaysWorkout>;
      workoutTemplates: () => Promise<WorkoutTemplate[]>;
    }
  ): Promise<{
    exercises: Exercise[];
    workoutLogs: WorkoutLogEntry[];
    todaysWorkout: TodaysWorkout;
    workoutTemplates: WorkoutTemplate[];
  }> {
    const requests = [
      {
        apiFunction: fetchers.exercises,
        dataType: 'exercises',
        params: {},
        options: { priority: 'medium' }
      },
      {
        apiFunction: fetchers.workoutLogs,
        dataType: 'workoutLogs',
        params: {},
        options: { priority: 'medium' }
      },
      {
        apiFunction: fetchers.todaysWorkout,
        dataType: 'todaysWorkout',
        params: {},
        options: { priority: 'high' }
      },
      {
        apiFunction: fetchers.workoutTemplates,
        dataType: 'workoutTemplates',
        params: {},
        options: { priority: 'low' }
      }
    ];

    const results = await this.optimizationManager.batchFetch(requests, 'dashboard');
    
    return {
      exercises: results[0],
      workoutLogs: results[1],
      todaysWorkout: results[2],
      workoutTemplates: results[3]
    };
  }

  /**
   * Batch fetch data for exercise list page
   */
  public async batchFetchExerciseListData(
    fetchers: {
      exercises: () => Promise<Exercise[]>;
      muscleGroups: () => Promise<string[]>;
    }
  ): Promise<{
    exercises: Exercise[];
    muscleGroups: string[];
  }> {
    const requests = [
      {
        apiFunction: fetchers.exercises,
        dataType: 'exercises',
        params: {},
        options: { priority: 'high' }
      },
      {
        apiFunction: fetchers.muscleGroups,
        dataType: 'muscleGroups',
        params: {},
        options: { priority: 'high' }
      }
    ];

    const results = await this.optimizationManager.batchFetch(requests, 'exerciseList');
    
    return {
      exercises: results[0],
      muscleGroups: results[1]
    };
  }

  /**
   * Prefetch data based on predicted navigation
   */
  public async prefetchForNavigation(targetPage: string): Promise<void> {
    await this.optimizationManager.prefetchForPage(targetPage);
  }

  /**
   * Track page navigation for optimization learning
   */
  public trackPageNavigation(fromPage: string, toPage: string, dataUsed: string[] = []): void {
    this.optimizationManager.trackNavigation(fromPage, toPage, dataUsed);
  }

  /**
   * Invalidate cache when data changes
   */
  public invalidateCache(dataTypes: string | string[]): void {
    this.optimizationManager.invalidateCache(dataTypes);
  }

  /**
   * Get optimization metrics and statistics
   */
  public getOptimizationMetrics() {
    return this.optimizationManager.getMetrics();
  }

  /**
   * Configure optimization behavior
   */
  public configure(config: {
    enableCaching?: boolean;
    enablePrefetching?: boolean;
    enableDeduplication?: boolean;
    enableBatching?: boolean;
    cacheSize?: number;
    defaultTTL?: number;
  }): void {
    this.optimizationManager.configure(config);
  }

  /**
   * Reset all optimization state
   */
  public reset(): void {
    this.optimizationManager.reset();
  }

  /**
   * Store references to original API functions for fallback
   */
  private storeOriginalFunctions(): void {
    // This would store references to the original API functions
    // Implementation would depend on the specific API structure
  }

  /**
   * Set up navigation tracking across different routing systems
   */
  private setupNavigationTracking(): void {
    // For React Router or similar client-side routing
    if (typeof window !== 'undefined') {
      let currentPath = window.location.pathname;
      
      // Listen for popstate events (back/forward navigation)
      window.addEventListener('popstate', () => {
        const newPath = window.location.pathname;
        this.trackPageNavigation(currentPath, newPath);
        currentPath = newPath;
      });

      // Listen for pushstate/replacestate (programmatic navigation)
      const originalPushState = history.pushState;
      const originalReplaceState = history.replaceState;

      history.pushState = function(...args) {
        const newPath = args[2]?.toString() || window.location.pathname;
        originalPushState.apply(history, args);
        ApiIntegrationLayer.getInstance().trackPageNavigation(currentPath, newPath);
        currentPath = newPath;
      };

      history.replaceState = function(...args) {
        const newPath = args[2]?.toString() || window.location.pathname;
        originalReplaceState.apply(history, args);
        ApiIntegrationLayer.getInstance().trackPageNavigation(currentPath, newPath);
        currentPath = newPath;
      };
    }
  }
}

/**
 * Wrapper functions for easy integration with existing code
 */

// Global instance
const integrationLayer = ApiIntegrationLayer.getInstance();

// Export wrapper functions that can replace existing API calls
export const optimizedFetchExercises = (
  originalFetcher: () => Promise<Exercise[]>,
  options?: { forceRefresh?: boolean; muscleGroup?: string; exercise?: string }
) => integrationLayer.fetchExercises(originalFetcher, options);

export const optimizedFetchMuscleGroups = (
  originalFetcher: () => Promise<string[]>,
  options?: { forceRefresh?: boolean }
) => integrationLayer.fetchMuscleGroups(originalFetcher, options);

export const optimizedFetchWorkoutTemplates = (
  originalFetcher: () => Promise<WorkoutTemplate[]>,
  options?: { forceRefresh?: boolean }
) => integrationLayer.fetchWorkoutTemplates(originalFetcher, options);

export const optimizedFetchWorkoutLogs = (
  originalFetcher: () => Promise<WorkoutLogEntry[]>,
  options?: {
    forceRefresh?: boolean;
    dateRange?: { start: string; end: string };
    muscleGroup?: string;
    exercise?: string;
  }
) => integrationLayer.fetchWorkoutLogs(originalFetcher, options);

export const optimizedFetchTodaysWorkout = (
  originalFetcher: () => Promise<TodaysWorkout>,
  options?: { forceRefresh?: boolean }
) => integrationLayer.fetchTodaysWorkout(originalFetcher, options);

// Batch operations
export const batchFetchDashboardData = integrationLayer.batchFetchDashboardData.bind(integrationLayer);
export const batchFetchExerciseListData = integrationLayer.batchFetchExerciseListData.bind(integrationLayer);

// Navigation and tracking
export const prefetchForNavigation = integrationLayer.prefetchForNavigation.bind(integrationLayer);
export const trackPageNavigation = integrationLayer.trackPageNavigation.bind(integrationLayer);

// Cache management
export const invalidateCache = integrationLayer.invalidateCache.bind(integrationLayer);
export const getOptimizationMetrics = integrationLayer.getOptimizationMetrics.bind(integrationLayer);
export const configureOptimization = integrationLayer.configure.bind(integrationLayer);
export const resetOptimization = integrationLayer.reset.bind(integrationLayer);

export default ApiIntegrationLayer;
