import { useUnifiedApp } from '@/contexts/ConsolidatedAppContext';
import { useUnifiedDataStore } from '@/store/unifiedDataStore';

// =============================================================================
// SPECIALIZED HOOKS FOR SPECIFIC DOMAINS
// =============================================================================

// Auth-specific hook
export const useAuth = () => {
  const {
    isAuthenticated,
    user,
    token,
    authLoading,
    authError,
    login,
    logout,
    initAuth,
  } = useUnifiedApp();
  
  return {
    isAuthenticated,
    user,
    token,
    loading: authLoading,
    error: authError,
    login,
    logout,
    initAuth,
  };
};

// Theme-specific hook
export const useTheme = () => {
  const {
    themeMode,
    toggleTheme,
    setThemeMode,
  } = useUnifiedApp();
  
  return {
    themeMode,
    toggleTheme,
    setThemeMode,
    isDark: themeMode === 'dark',
    isLight: themeMode === 'light',
  };
};

// Navigation-specific hook
export const useNavigation = () => {
  const {
    currentView,
    setCurrentView,
    isSidebarCollapsed,
    toggleSidebar,
    setIsSidebarCollapsed,
  } = useUnifiedApp();
  
  return {
    currentView,
    setCurrentView,
    isSidebarCollapsed,
    toggleSidebar,
    setIsSidebarCollapsed,
  };
};

// Data status hook
export const useDataStatus = () => {
  const {
    isDataLoading,
    hasDataErrors,
    isOffline,
    fetchAllData,
    clearAllData,
  } = useUnifiedApp();
  
  return {
    isLoading: isDataLoading,
    hasErrors: hasDataErrors,
    isOffline,
    fetchAllData,
    clearAllData,
  };
};

// =============================================================================
// DATA-SPECIFIC HOOKS
// =============================================================================

// Workout logs hook - provides access to workout log data and actions
export const useWorkoutLogs = () => {
  // Split into individual selectors to prevent object recreation
  const workoutLogs = useUnifiedDataStore(state => state.workoutLogs);
  const loading = useUnifiedDataStore(state => state.loading.workoutLogs);
  const error = useUnifiedDataStore(state => state.errors.workoutLogs);
  const dataFetched = useUnifiedDataStore(state => state.dataFetched.workoutLogs);
  const fetchWorkoutLogs = useUnifiedDataStore(state => state.fetchWorkoutLogs);
  const addWorkoutLog = useUnifiedDataStore(state => state.addWorkoutLog);
  const editWorkoutLog = useUnifiedDataStore(state => state.editWorkoutLog);
  const removeWorkoutLog = useUnifiedDataStore(state => state.removeWorkoutLog);
  const syncPendingLogs = useUnifiedDataStore(state => state.syncPendingLogs);
  const getPreviousHighestVolumeWorkout = useUnifiedDataStore(state => state.getPreviousHighestVolumeWorkout);
  
  return {
    workoutLogs,
    isLoading: loading,
    error,
    isDataFetched: dataFetched,
    fetchWorkoutLogs,
    addWorkoutLog,
    editWorkoutLog,
    removeWorkoutLog,
    syncPendingLogs,
    getPreviousHighestVolumeWorkout,
  };
};

