/**
 * Utility for more robust Google API initialization
 */
import { gapi } from 'gapi-script';
import { googleConfig } from '@/config/google.config';
import { validateGoogleConfig, logConfigValidation } from '@/utils/authValidation';

// Declare global gapi type to avoid TypeScript errors
declare global {
  interface Window {
    gapi: typeof gapi;
  }
}

let isInitializing = false;
let initializationCompleted = false;
let initializationPromise: Promise<boolean> | null = null;

/**
 * Initialize the Google API client with proper error handling and retries
 * @param options Optional configuration including retry count and timeout
 * @returns Promise that resolves when initialization is complete
 */
export async function initializeGoogleApi(options = { 
  retries: 3,
  timeout: 15000
}): Promise<boolean> {
  // Validate configuration before attempting initialization
  const configValidation = validateGoogleConfig();
  if (!configValidation.isValid) {
    console.error('Google API configuration validation failed:');
    configValidation.errors.forEach(error => console.error(`  • ${error}`));
    throw new Error('Google API configuration is invalid. Please check your environment variables.');
  }

  // Log configuration status
  logConfigValidation();

  // Return existing promise if initialization is already in progress
  if (isInitializing && initializationPromise) {
    console.log('Google API initialization already in progress, returning existing promise');
    return initializationPromise;
  }
  
  // If already initialized successfully, verify it's still working
  if (initializationCompleted && gapi?.client?.sheets && gapi?.auth2?.getAuthInstance()) {
    try {
      const authInstance = gapi.auth2.getAuthInstance();
      if (authInstance && typeof authInstance.isSignedIn?.get === 'function') {
        console.log('Google API already initialized and working');
        return true;
      }
    } catch (error) {
      console.warn('Previously initialized auth instance is no longer valid, re-initializing:', error);
      initializationCompleted = false;
    }
  }

  isInitializing = true;
  let currentRetry = 0;
  
  // Create and store the initialization promise
  initializationPromise = (async (): Promise<boolean> => {
    // Combine all scopes
    const scopes = [
      ...googleConfig.sheets.scopes,
      ...googleConfig.drive.scopes,
      'profile',
      'email'
    ].join(' ');
    
    const initialize = async (): Promise<boolean> => {
      try {
        // Ensure gapi script is loaded first
        if (!gapi) {
          throw new Error('Google API script not loaded. Please ensure gapi-script is properly loaded.');
        }

        // First, load the client library if needed
        if (!gapi.client) {
          await new Promise<void>((resolve, reject) => {
            const timeoutId = setTimeout(() => {
              reject(new Error('Timeout loading gapi.client'));
            }, options.timeout);
            
            gapi.load('client', {
              callback: () => {
                clearTimeout(timeoutId);
                resolve();
              },              onerror: (err: unknown) => {
                clearTimeout(timeoutId);
                reject(err || new Error('Failed to load gapi.client'));
              }
            });
          });
        }
        
        // Then load auth2 if needed
        if (!gapi.auth2) {
          await new Promise<void>((resolve, reject) => {
            const timeoutId = setTimeout(() => {
              reject(new Error('Timeout loading gapi.auth2'));
            }, options.timeout);
            
            gapi.load('auth2', {
              callback: () => {
                clearTimeout(timeoutId);
                resolve();
              },              onerror: (err: unknown) => {
                clearTimeout(timeoutId);
                reject(err || new Error('Failed to load gapi.auth2'));
              }
            });
          });
        }
        
        // Validate environment variables
        const apiKey = import.meta.env.VITE_GOOGLE_API_KEY;
        const clientId = googleConfig.auth.clientId;
        
        if (!apiKey) {
          throw new Error('VITE_GOOGLE_API_KEY environment variable is not set');
        }
        
        if (!clientId) {
          throw new Error('Google Client ID is not configured');
        }
        
        // Initialize the client with proper error handling
        await gapi.client.init({
          apiKey,
          clientId,
          scope: scopes,
          discoveryDocs: ['https://sheets.googleapis.com/$discovery/rest?version=v4'],
        });
        
        // Verify auth2 initialization
        const authInstance = gapi.auth2.getAuthInstance();
        if (!authInstance) {
          throw new Error('Failed to get Google Auth instance after initialization');
        }
        
        // Load the Sheets API separately to ensure it's available
        if (!gapi.client.sheets) {
          await new Promise<void>((resolve, reject) => {
            gapi.client.load('sheets', 'v4')
              .then(() => {
                if (!gapi.client.sheets) {
                  reject(new Error('Google Sheets API failed to load properly'));
                } else {
                  resolve();
                }
              })
              .catch(reject);
          });
        }
        
        console.log('Google API initialization completed successfully');
        isInitializing = false;
        initializationCompleted = true;
        return true;
      } catch (error) {
        console.error(`Google API initialization attempt ${currentRetry + 1} failed:`, error);
        
        if (currentRetry < options.retries - 1) {
          currentRetry++;
          // Exponential backoff with jitter
          const delay = Math.min(1000 * Math.pow(2, currentRetry) + Math.random() * 1000, 10000);
          console.log(`Retrying Google API initialization in ${delay}ms...`);
          await new Promise(resolve => setTimeout(resolve, delay));
          return initialize();
        } else {
          isInitializing = false;
          initializationPromise = null;
          throw error;
        }
      }
    };
    
    return initialize();
  })();
  
  try {
    const result = await initializationPromise;
    return result;
  } catch (error) {
    isInitializing = false;
    initializationPromise = null;
    console.error('Failed to initialize Google API after multiple attempts:', error);
    return false;
  }
}

/**
 * Reset initialization state - useful for testing or force re-initialization
 */
export function resetInitializationState(): void {
  isInitializing = false;
  initializationCompleted = false;
  initializationPromise = null;
}

/**
 * Check if we need to refresh the access token and handle it
 */
export async function ensureValidToken(): Promise<string | null> {
  try {
    if (!gapi?.auth2?.getAuthInstance()?.isSignedIn.get()) {
      console.warn('User is not signed in');
      return null;
    }
    
    const googleUser = gapi.auth2.getAuthInstance().currentUser.get();
    if (!googleUser) {
      console.warn('No Google user found');
      return null;
    }
    
    const authResponse = googleUser.getAuthResponse();
    if (!authResponse) {
      console.warn('No auth response found');
      return null;
    }
    
    // Check if token will expire soon (within 5 minutes)
    const willExpireSoon = (authResponse.expires_at - Date.now()) < 5 * 60 * 1000;
    
    if (willExpireSoon) {
      try {
        console.log('Token expiring soon, refreshing...');
        // Refresh the token
        await googleUser.reloadAuthResponse();
        // Get the new token
        const newAuthResponse = googleUser.getAuthResponse();
        console.log('Token refreshed successfully');
        return newAuthResponse.access_token;
      } catch (error) {
        console.error('Error refreshing Google access token:', error);
        return null;
      }
    }
      return authResponse.access_token;
  } catch (error) {
    console.error('Error in ensureValidToken:', error);
    return null;
  }
}

/**
 * Check if the Google API is properly initialized
 */
export function isGoogleApiInitialized(): boolean {
  return initializationCompleted && 
         !!gapi?.client?.sheets && 
         !!gapi?.auth2?.getAuthInstance();
}

/**
 * Get the current authentication status
 */
export function getAuthStatus(): { isSignedIn: boolean; isInitialized: boolean } {
  try {
    const isInitialized = isGoogleApiInitialized();
    const isSignedIn = isInitialized && gapi.auth2.getAuthInstance().isSignedIn.get();
    
    return { isSignedIn, isInitialized };
  } catch (error) {
    console.error('Error getting auth status:', error);
    return { isSignedIn: false, isInitialized: false };
  }
}
