import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { googleConfig } from '../config/google.config';
import { initializeGoogleApi } from '../lib/googleApiHelper';

// Define User interface
export interface User {
  id: string;
  name: string;
  email: string;
  picture?: string;
}

// Define View types
export type ViewType = 'dashboard' | 'workouts' | 'exercises' | 'schedule' | 'stats' | 'profile' | 'settings' | 'login' | 'test' | 'templates' | 'workout' | 'muscleFatigue';

// Define GoogleAuth interfaces
interface GoogleUser {
  getBasicProfile: () => {
    getId: () => string;
    getName: () => string;
    getEmail: () => string;
    getImageUrl: () => string;
  };
  getAuthResponse: (includeAuthorizationData?: boolean) => {
    access_token: string;
    id_token: string;
    scope: string;
    expires_in: number;
    first_issued_at: number;
    expires_at: number;
  };
}

interface GoogleAuth {
  signIn: (options?: object) => Promise<GoogleUser>;
  signOut: () => Promise<void>;
  isSignedIn: {
    get: () => boolean;
  };
  currentUser: {
    get: () => GoogleUser;
  };
}

// Define AppStore state
interface AppState {
  // Auth state
  isAuthenticated: boolean;
  user: User | null;
  token: string | null;
  googleAuth: GoogleAuth | null;
  authLoading: boolean;
  authError: string | null;
  
  // View state
  currentView: ViewType;
  
  // Theme state
  themeMode: 'light' | 'dark';
  
  // Sidebar state
  isSidebarCollapsed: boolean;

  // Auth actions
  login: () => Promise<void>;
  logout: () => Promise<void>;
  initAuth: () => Promise<void>;
  
  // View actions
  setCurrentView: (view: ViewType) => void;
  
  // Theme actions
  toggleTheme: () => void;
  setThemeMode: (theme: 'light' | 'dark') => void;

  // Sidebar actions
  toggleSidebar: () => void;
  setIsSidebarCollapsed: (collapsed: boolean) => void;
}

export const useAppStore = create<AppState>()(
  persist(
    (set, get) => ({
      // Auth state
      isAuthenticated: false,
      user: null,
      token: null,
      googleAuth: null,
      authLoading: false,
      authError: null,
      
      // View state
      currentView: 'dashboard',
      
      // Theme state
      themeMode: 
        typeof window !== 'undefined' && window.matchMedia && 
        window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light',
      
      // Sidebar state initial value
      isSidebarCollapsed: false,      // Auth actions
      initAuth: async () => {
        const currentState = get();
        
        // Prevent multiple simultaneous initializations
        if (currentState.authLoading) {
          console.log('Auth initialization already in progress');
          return;
        }
        
        set({ authLoading: true, authError: null });
        
        try {
          console.log('Starting Google Auth initialization...');
          
          // Use the robust Google API initialization helper
          const initialized = await initializeGoogleApi({ 
            retries: 3, 
            timeout: 20000 
          });
          
          if (!initialized) {
            throw new Error('Failed to initialize Google API after multiple attempts');
          }

          // Import gapi after initialization
          const { gapi } = await import('gapi-script');
          
          // Get the auth instance
          const authInstance = gapi.auth2.getAuthInstance();
          
          if (!authInstance) {
            throw new Error('Google Auth instance not available');
          }

          console.log('Google Auth instance obtained successfully');
          
          // Check if the user is already signed in
          if (authInstance.isSignedIn.get()) {
            console.log('User is already signed in, updating state...');
            
            const googleUser = authInstance.currentUser.get();
            const profile = googleUser.getBasicProfile();
            const authResponse = googleUser.getAuthResponse(true);
            
            // Validate the auth response
            if (!authResponse || !authResponse.access_token) {
              throw new Error('Invalid authentication response');
            }
            
            set({
              isAuthenticated: true,
              user: {
                id: profile.getId(),
                name: profile.getName(),
                email: profile.getEmail(),
                picture: profile.getImageUrl()
              },
              token: authResponse.access_token,
              googleAuth: authInstance,
              authLoading: false,
              authError: null
            });
            
            console.log('Authentication state updated successfully');
          } else {
            console.log('User not signed in, setting auth instance only');
            set({ 
              googleAuth: authInstance, 
              authLoading: false,
              authError: null
            });
          }
        } catch (err) {
          console.error('Error initializing Google Auth:', err);
          
          // Provide more specific error messages
          let errorMessage = 'Failed to initialize authentication';
          if (err instanceof Error) {
            if (err.message.includes('API_KEY')) {
              errorMessage = 'Google API key not configured. Please check environment variables.';
            } else if (err.message.includes('CLIENT_ID')) {
              errorMessage = 'Google Client ID not configured. Please check configuration.';
            } else if (err.message.includes('script not loaded')) {
              errorMessage = 'Google API script failed to load. Please check your internet connection.';
            } else {
              errorMessage = err.message;
            }
          }
          
          set({ 
            authError: errorMessage,
            authLoading: false,
            googleAuth: null
          });
        }
      },
        login: async () => {
        set({ authLoading: true, authError: null });
        try {
          const { googleAuth } = get();
          if (!googleAuth) {
            // Try to initialize auth if not already done
            await get().initAuth();
            const updatedState = get();
            if (!updatedState.googleAuth) {
              throw new Error('Google Auth initialization failed');
            }
          }

          const authInstance = get().googleAuth!;
          
          // Trigger the Google sign-in flow
          const googleUser = await authInstance.signIn({
            scope: [
              ...googleConfig.sheets.scopes, 
              ...googleConfig.drive.scopes,
              'profile',
              'email'
            ].join(' ')
          });
          
          const profile = googleUser.getBasicProfile();
          const authResponse = googleUser.getAuthResponse(true);

          set({
            isAuthenticated: true,
            user: {
              id: profile.getId(),
              name: profile.getName(),
              email: profile.getEmail(),
              picture: profile.getImageUrl()
            },
            token: authResponse.access_token,
            authLoading: false,
            authError: null
          });
        } catch (err) {
          console.error('Login error:', err);
          set({ 
            authError: err instanceof Error ? err.message : 'Login failed. Please try again.',
            authLoading: false
          });
        }
      },
      
      logout: async () => {
        set({ authLoading: true });
        try {
          const { googleAuth } = get();
          if (googleAuth) {
            await googleAuth.signOut();
          }
          
          set({
            isAuthenticated: false,
            user: null,
            token: null,
            authLoading: false,
            authError: null
          });
        } catch (err) {
          console.error('Logout error:', err);
          set({ 
            authError: 'Logout failed. Please try again.',
            authLoading: false
          });
        }
      },
      
      // View actions
      setCurrentView: (view: ViewType) => {
        set({ currentView: view });
      },
      
      // Theme actions
      toggleTheme: () => {
        const newTheme = get().themeMode === 'light' ? 'dark' : 'light';
        set({ themeMode: newTheme });
        
        // Apply theme to document
        if (typeof document !== 'undefined') {
          document.documentElement.classList.remove('light', 'dark');
          document.documentElement.classList.add(newTheme);
          localStorage.setItem('theme', newTheme);
        }
      },
      
      // Directly set theme mode
      setThemeMode: (theme: 'light' | 'dark') => {
        set({ themeMode: theme });
        
        // Apply theme to document
        if (typeof document !== 'undefined') {
          document.documentElement.classList.remove('light', 'dark');
          document.documentElement.classList.add(theme);
          localStorage.setItem('theme', theme);
        }
      },
      
      // Sidebar actions
      toggleSidebar: () => {
        set((state) => ({ isSidebarCollapsed: !state.isSidebarCollapsed }));
      },
      setIsSidebarCollapsed: (collapsed: boolean) => {
        set({ isSidebarCollapsed: collapsed });
      }
    }),
    {
      name: 'workout-app-storage',
      partialize: (state) => ({
        themeMode: state.themeMode,
        token: state.token,
        user: state.user,
        isAuthenticated: state.isAuthenticated,
        currentView: state.currentView,
        isSidebarCollapsed: state.isSidebarCollapsed // Persist sidebar state
      })
    }
  )
);
