// Integration file to connect our optimization system with existing app-wrkot codebase
import ApiOptimizationManager from './ApiOptimizationManager';
import ApiIntegrationLayer from './ApiIntegrationLayer';

// Import the existing API functions from the app-wrkot codebase
import { readSpreadsheetData, GenericSheetRowData } from '@/lib/spreadsheetAPI';
import { Exercise } from '@/types/Exercises';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import { WorkoutTemplate } from '@/types/Workout_Templates';

// Create singleton instances using getInstance methods
const optimizationManager = ApiOptimizationManager.getInstance();

// Integration layer instance  
const integrationLayer = ApiIntegrationLayer.getInstance();

// Enhanced data fetchers that replace the existing ones in unifiedDataStore
export const optimizedDataFetchers = {
  
  /**
   * Optimized exercises fetcher - replaces the existing fetchExercises function
   */
  async fetchExercises(forceRefresh = false): Promise<{
    success: boolean;
    data?: Exercise[];
    error?: string;
    cached?: boolean;
  }> {    try {
      // Use the optimization manager for intelligent caching and deduplication
      const result = await optimizationManager.optimizedFetch(
        async () => {
          const sheetResult = await readSpreadsheetData<GenericSheetRowData>('Exercises');
          
          if (!sheetResult.success || !sheetResult.data) {
            throw new Error(sheetResult.error || 'Failed to fetch exercises');
          }

          const exercisesData: Exercise[] = sheetResult.data.map((row: GenericSheetRowData) => ({
            muscleGroup: String(row['Muscle_Group'] || ''),
            exercise: String(row['Exercise'] || ''),
            difficultyLevel: String(row['Difficulty_Level'] || ''),
            equipmentRequired: String(row['Equipment_Required'] || ''),
            targetIntensity: String(row['Target_Intensity'] || ''),
            primaryMuscleGroup: String(row['Primary_Muscle_Group'] || ''),
            secondaryMuscleGroup: String(row['Secondary_Muscle_Group'] || '') || null,
            exerciseDuration: String(row['Exercise_Duration'] || ''),
            recoveryTime: String(row['Recovery_Time'] || ''),
            exerciseType: String(row['Exercise_Type'] || ''),
            caloriesBurned: Number(row['Calories_Burned'] || 0),
            exerciseProgression: String(row['Exercise_Progression'] || ''),
            injuryRiskLevel: String(row['Injury_Risk_Level'] || ''),
            exerciseLink: String(row['exercise_link'] || ''),
            imageLink: String(row['image_link'] || ''),
            relatedPath: String(row['related_path'] || ''),   
          })).filter((exercise: Exercise) => exercise.muscleGroup && exercise.exercise);

          return exercisesData;
        },
        'exercises',
        {},
        {
          forceRefresh,
          dependencies: ['user-preferences'] // If user changes preferences, invalidate exercises
        }
      );

      return {
        success: true,
        data: result,
        cached: false // optimization manager doesn't return cache status in this version
      };
    } catch (error) {
      console.error('Error in optimized fetchExercises:', error);
      return {
        success: false,
        error: error instanceof Error ? error.message : 'Unknown error'
      };
    }
  },

  /**
   * Optimized workout templates fetcher
   */
  async fetchWorkoutTemplates(forceRefresh = false): Promise<{
    success: boolean;
    data?: WorkoutTemplate[];
    error?: string;
    cached?: boolean;
  }> {    try {
      const result = await optimizationManager.optimizedFetch(
        async () => {
          const sheetResult = await readSpreadsheetData<GenericSheetRowData>('WorkoutTemplates');
          
          if (!sheetResult.success || !sheetResult.data) {
            throw new Error(sheetResult.error || 'Failed to fetch workout templates');
          }

          const templatesData: WorkoutTemplate[] = sheetResult.data.map((row: GenericSheetRowData) => {
            const exercisesStr = String(row['Exercises'] || '[]');
            let exercises = [];
            try {
              exercises = JSON.parse(exercisesStr);
            } catch (error) {
              console.error('Failed to parse exercises JSON for template:', row['Name'], error);
            }
            
            return {
              name: String(row['Name'] || ''),
              description: String(row['Description'] || ''),
              exercises,
              createdAt: String(row['CreatedAt'] || new Date().toISOString()),
              lastUsed: String(row['LastUsed'] || '')
            };
          }).filter((template: WorkoutTemplate) => template.name);

          return templatesData;
        },
        'workoutTemplates',
        {},
        {
          forceRefresh,
          dependencies: ['exercises'] // If exercises change, templates might need refresh
        }
      );

      return {
        success: true,
        data: result,
        cached: false
      };
    } catch (error) {
      console.error('Error in optimized fetchWorkoutTemplates:', error);
      return {
        success: false,
        error: error instanceof Error ? error.message : 'Unknown error'
      };
    }
  },

  /**
   * Optimized workout logs fetcher
   */
  async fetchWorkoutLogs(forceRefresh = false): Promise<{
    success: boolean;
    data?: WorkoutLogEntry[];
    error?: string;
    cached?: boolean;
  }> {    try {
      const result = await optimizationManager.optimizedFetch(
        async () => {
          const sheetResult = await readSpreadsheetData<GenericSheetRowData>('Workout_Logs');
          
          if (!sheetResult.success || !sheetResult.data) {
            throw new Error(sheetResult.error || 'Failed to fetch workout logs');
          }

          const generateId = () => `log_${new Date().getTime()}_${Math.random().toString(36).substring(2, 9)}`;

          const logsWithClientFields: WorkoutLogEntry[] = sheetResult.data.map((rawRow: GenericSheetRowData, index: number) => {
            const mappedRow = {
              date: String(rawRow['Date'] || ''),
              muscleGroup: String(rawRow['Muscle_Group'] || ''),
              exercise: String(rawRow['Exercise'] || ''),
              reps: Number(rawRow['Reps'] || 0),
              weight: Number(rawRow['Weight'] || 0),
              rating: Number(rawRow['Rating'] || 0),
              restTime: rawRow['Rest_Time'] ? Number(rawRow['Rest_Time']) : null,
            };

            // Validate essential data
            if (!mappedRow.date || !mappedRow.exercise) {
              console.warn('Skipping row due to missing essential data (date/exercise):', rawRow);
              return null;
            }

            if (isNaN(mappedRow.reps)) mappedRow.reps = 0;
            if (isNaN(mappedRow.weight)) mappedRow.weight = 0;
            if (isNaN(mappedRow.rating)) mappedRow.rating = 0;
            if (mappedRow.restTime !== null && isNaN(mappedRow.restTime)) mappedRow.restTime = null;

            return {
              ...mappedRow,
              id: generateId(),
              rowIndex: index + 2,
              isSynced: true,
            } as WorkoutLogEntry;
          }).filter(log => log !== null) as WorkoutLogEntry[];

          return logsWithClientFields;
        },
        'workoutLogs',
        {},
        {
          forceRefresh,
          dependencies: ['user-session'] // Logs depend on user session
        }
      );

      return {
        success: true,
        data: result,
        cached: false
      };
    } catch (error) {
      console.error('Error in optimized fetchWorkoutLogs:', error);
      return {
        success: false,
        error: error instanceof Error ? error.message : 'Unknown error'
      };
    }
  },

  /**
   * Batch fetch multiple data types - for dashboard and initial load
   */
  async batchFetchData(dataTypes: string[], forceRefresh = false): Promise<{
    [key: string]: {
      success: boolean;
      data?: any;
      error?: string;
      cached?: boolean;
    }
  }> {
    const results: { [key: string]: any } = {};

    // Use Promise.all for parallel fetching but with smart deduplication
    await Promise.all(dataTypes.map(async (dataType) => {
      try {
        switch (dataType) {
          case 'exercises':
            results[dataType] = await this.fetchExercises(forceRefresh);
            break;
          case 'workoutTemplates':
            results[dataType] = await this.fetchWorkoutTemplates(forceRefresh);
            break;
          case 'workoutLogs':
            results[dataType] = await this.fetchWorkoutLogs(forceRefresh);
            break;
          default:
            results[dataType] = {
              success: false,
              error: `Unknown data type: ${dataType}`
            };
        }
      } catch (error) {
        results[dataType] = {
          success: false,
          error: error instanceof Error ? error.message : 'Unknown error'
        };
      }
    }));

    return results;
  }
};

// Navigation tracking integration
export const navigationTracker = {
  /**
   * Track page navigation for prefetching optimization
   */
  trackNavigation(fromPage: string, toPage: string) {
    optimizationManager.trackNavigation(fromPage, toPage);
  },

  /**
   * Prefetch data based on navigation patterns
   */
  async prefetchForPage(targetPage: string) {
    await optimizationManager.prefetchForPage(targetPage);
  }
};

// Performance monitoring integration
export const performanceMonitor = {
  /**
   * Get optimization metrics
   */
  getMetrics() {
    return optimizationManager.getMetrics();
  }
};

// Cache management utilities
export const cacheManager = {
  /**
   * Invalidate specific cache entries
   */
  invalidateCache(keys: string[]) {
    optimizationManager.invalidateCache(keys);
  }
};

// Export the optimization manager for direct access if needed
export { optimizationManager, integrationLayer };

