import { create } from 'zustand';
import { persist, createJSONStorage, subscribeWithSelector } from 'zustand/middleware';
import { produce } from 'immer';
import { v4 as uuidv4 } from 'uuid';

// Custom immer middleware - simplified for compatibility
const immer = <T>(
  initializer: (set: (fn: (state: T) => void) => void, get: () => T, store: unknown) => T
) => 
  (set: (state: T | Partial<T> | ((state: T) => T | Partial<T>)) => void, get: () => T, store: unknown) =>
    initializer(
      (fn: (state: T) => void) => set(produce(fn) as (state: T) => T),
      get,
      store
    );

// Type imports from existing stores
import { Exercise } from '@/types/Exercises';
import { WorkoutLogEntry } from '@/types/Workout_Log';
import { WorkoutTemplate, ActiveWorkout } from '@/types/Workout_Templates';

// Import app store for authentication state
import { useAppStore } from './appStore';

// Helper function to check authentication status
const checkAuthenticationStatus = (): { isAuthenticated: boolean; hasToken: boolean; authError: string | null } => {
  try {
    const appState = useAppStore.getState();
    return {
      isAuthenticated: appState.isAuthenticated,
      hasToken: !!appState.token,
      authError: appState.authError
    };
  } catch (error) {
    console.error('Error checking authentication status:', error);
    return {
      isAuthenticated: false,
      hasToken: false,
      authError: 'Failed to check authentication status'
    };
  }
};

// Define type for workout analysis result
interface WorkoutAnalysis {
  date: string;
  totalVolume: number;
  maxWeight: number;
  totalReps: number;
  weight: number;
  reps: number;
  highestVolumeDate: string;
  totalSets: number;
  allSets: Array<{
    id: string;
    weight: number;
    reps: number;
    rating: number;
    restTime: number | null | undefined;
    volume: number;
  }>;
  highestVolume: {
    date: string;
    totalVolume: number;
    maxWeight: number;
    totalReps: number;
    weight: number;
    reps: number;
  };
  progressionTargets: {
    targetVolume: number;
    progressionRate: number;
    sameWeightMoreReps: {
      weight: number;
      reps: number;
      additionalReps: number;
      sets: number;
    };
    moreSets: {
      weight: number;
      reps: number;
      additionalSets: number;
      sets: number;
    };
    balancedProgression: {
      weight: number;
      reps: number;
      additionalReps: number;
      sets: number;
      additionalSets: number;
    };
  };
}

