import React, { createContext, useContext, useEffect, ReactNode } from 'react';
import { useAppStore, User, ViewType } from '@/store/appStore';
import { useUnifiedDataStore } from '@/store/unifiedDataStore';

// =============================================================================
// UNIFIED APP CONTEXT INTERFACE
// =============================================================================

interface UnifiedAppContextType {
  // Auth state and actions from appStore
  isAuthenticated: boolean;
  user: User | null;
  token: string | null;
  authLoading: boolean;
  authError: string | null;
  login: () => Promise<void>;
  logout: () => Promise<void>;
  initAuth: () => Promise<void>;
  
  // Theme state and actions from appStore
  themeMode: 'light' | 'dark';
  toggleTheme: () => void;
  setThemeMode: (theme: 'light' | 'dark') => void;
  
  // View state and actions from appStore
  currentView: ViewType;
  setCurrentView: (view: ViewType) => void;
  
  // Sidebar state and actions from appStore
  isSidebarCollapsed: boolean;
  toggleSidebar: () => void;
  setIsSidebarCollapsed: (collapsed: boolean) => void;
  
  // Data actions from unifiedDataStore
  fetchAllData: (forceRefresh?: boolean) => Promise<void>;
  clearAllData: () => void;
  
  // Loading states
  isDataLoading: boolean;
  hasDataErrors: boolean;
  
  // Offline state
  isOffline: boolean;
}

// =============================================================================
// CONTEXT CREATION
// =============================================================================

const UnifiedAppContext = createContext<UnifiedAppContextType | undefined>(undefined);

// =============================================================================
// CONTEXT PROVIDER
// =============================================================================

interface UnifiedAppProviderProps {
  children: ReactNode;
}

export const UnifiedAppProvider: React.FC<UnifiedAppProviderProps> = ({ children }) => {
  // App store selectors (auth, theme, view, sidebar)
  const {
    isAuthenticated,
    user,
    token,
    authLoading,
    authError,
    login,
    logout,
    initAuth,
    themeMode,
    toggleTheme,
    setThemeMode,
    currentView,
    setCurrentView,
    isSidebarCollapsed,
    toggleSidebar,
    setIsSidebarCollapsed,
  } = useAppStore();
  // Unified data store selectors
  const {
    loading,
    errors,
    fetchAllData: fetchAllDataStore,
    clearAllData,
  } = useUnifiedDataStore();

  // Local state for offline detection
  const [isOffline, setIsOffline] = React.useState(!navigator.onLine);
  // Memoized wrapper for fetchAllData to prevent infinite re-renders
  const fetchAllData = React.useCallback(async (forceRefresh?: boolean) => {
    try {
      console.log('ConsolidatedAppContext: Starting fetchAllData with auth check...');
      
      // Double-check authentication state before calling store method
      if (!isAuthenticated || !token || authLoading || authError) {
        console.warn('ConsolidatedAppContext: Skipping fetchAllData - authentication not ready', {
          isAuthenticated,
          hasToken: !!token,
          authLoading,
          authError
        });
        return;
      }

      await fetchAllDataStore(forceRefresh);
    } catch (error) {
      console.error('ConsolidatedAppContext: Error in fetchAllData:', error);
      
      // If the error is authentication-related, try to re-initialize auth
      if (error instanceof Error && 
          (error.message.includes('authentication') || 
           error.message.includes('token') ||
           error.message.includes('401'))) {
        console.log('ConsolidatedAppContext: Auth error detected, attempting to re-initialize...');
        try {
          await initAuth();
        } catch (authError) {
          console.error('ConsolidatedAppContext: Failed to re-initialize auth:', authError);
        }
      }
    }
  }, [fetchAllDataStore, isAuthenticated, token, authLoading, authError, initAuth]);

  // =============================================================================
  // EFFECTS
  // =============================================================================
  // Initialize authentication on mount
  useEffect(() => {
    initAuth();
  }, [initAuth]);

  // Initialize data fetching when authenticated - but add proper guards
  useEffect(() => {
    if (isAuthenticated && !authLoading && !authError && token && !isOffline) {
      console.log('ConsolidatedAppContext: Starting data fetch - authentication verified');
      fetchAllData();
    } else if (isAuthenticated && !token) {
      console.warn('ConsolidatedAppContext: User authenticated but no token available, skipping data fetch');
    } else if (authError) {
      console.warn('ConsolidatedAppContext: Authentication error present, skipping data fetch:', authError);
    } else if (authLoading) {
      console.log('ConsolidatedAppContext: Authentication still loading, waiting...');
    }
  }, [isAuthenticated, authLoading, authError, token, isOffline, fetchAllData]);
  // Handle online/offline events
  useEffect(() => {
    const handleOnline = () => {
      setIsOffline(false);
      // Only auto-sync when coming back online if we have a valid authenticated state
      if (isAuthenticated && !authLoading && !authError && token) {
        console.log('ConsolidatedAppContext: Back online, refreshing data...');
        fetchAllData();
      }
    };

    const handleOffline = () => {
      setIsOffline(true);
      console.log('ConsolidatedAppContext: Gone offline');
    };

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, [isAuthenticated, authLoading, authError, token, fetchAllData]);

  // Apply theme on mount and changes
  useEffect(() => {
    if (typeof document !== 'undefined') {
      document.documentElement.classList.remove('light', 'dark');
      document.documentElement.classList.add(themeMode);
    }
  }, [themeMode]);

  // =============================================================================
  // COMPUTED VALUES
  // =============================================================================

  const isDataLoading = loading.exercises || loading.workoutLogs || loading.templates || loading.general;
  const hasDataErrors = Boolean(
    errors.exercises || 
    errors.workoutLogs || 
    errors.templates || 
    errors.general
  );

  // =============================================================================
  // CONTEXT VALUE
  // =============================================================================

  const contextValue: UnifiedAppContextType = {
    // Auth
    isAuthenticated,
    user,
    token,
    authLoading,
    authError,
    login,
    logout,
    initAuth,
    
    // Theme
    themeMode,
    toggleTheme,
    setThemeMode,
    
    // View
    currentView,
    setCurrentView,
    
    // Sidebar
    isSidebarCollapsed,
    toggleSidebar,
    setIsSidebarCollapsed,
    
    // Data
    fetchAllData,
    clearAllData,
    
    // State
    isDataLoading,
    hasDataErrors,
    isOffline,
  };

  return (
    <UnifiedAppContext.Provider value={contextValue}>
      {children}
    </UnifiedAppContext.Provider>
  );
};

// =============================================================================
// MAIN CONTEXT HOOK
// =============================================================================

export const useUnifiedApp = (): UnifiedAppContextType => {
  const context = useContext(UnifiedAppContext);
  if (context === undefined) {
    throw new Error('useUnifiedApp must be used within a UnifiedAppProvider');
  }
  return context;
};

export default UnifiedAppProvider;
