/**
 * API Optimization Manager for Workout Application
 * 
 * This manager integrates with existing API clients and stores to provide:
 * - Smart caching with TTL and dependency management
 * - Request deduplication and batching
 * - Navigation-based prefetching
 * - Performance monitoring and cache statistics
 */

import { Exercise } from '../app-wrkot/src/types/Exercises';
import { WorkoutTemplate } from '../app-wrkot/src/types/Workout_Templates';
import { WorkoutLogEntry } from '../app-wrkot/src/types/Workout_Log';

// Cache entry interface
interface CacheEntry<T> {
  data: T;
  timestamp: number;
  expiresAt: number;
  accessCount: number;
  lastAccessed: number;
  dependencies?: string[];
  stale?: boolean;
}

// Request tracking interface
interface PendingRequest {
  promise: Promise<any>;
  timestamp: number;
  subscribers: Array<(data: any) => void>;
}

// Navigation pattern interface
interface NavigationPattern {
  fromPage: string;
  toPage: string;
  frequency: number;
  lastOccurrence: number;
  dataNeeded: string[];
}

// Performance metrics interface
interface PerformanceMetrics {
  cacheHits: number;
  cacheMisses: number;
  requestsDeduplicated: number;
  prefetchSuccess: number;
  prefetchFailure: number;
  totalRequests: number;
  averageResponseTime: number;
  lastResetTime: number;
}

// Configuration interface
interface OptimizationConfig {
  enableCaching: boolean;
  enablePrefetching: boolean;
  enableDeduplication: boolean;
  enableBatching: boolean;
  cacheSize: number;
  defaultTTL: number;
  navigationTrackingEnabled: boolean;
  performanceMonitoringEnabled: boolean;
}

class ApiOptimizationManager {
  private static instance: ApiOptimizationManager;
  
  // Core data storage
  private cache = new Map<string, CacheEntry<any>>();
  private pendingRequests = new Map<string, PendingRequest>();
  private navigationPatterns = new Map<string, NavigationPattern>();
  private batchQueue = new Map<string, Array<() => Promise<any>>>();
  
  // State tracking
  private currentPage = '';
  private navigationHistory: string[] = [];
  private lastNavigationTime = 0;
  
  // Performance metrics
  private metrics: PerformanceMetrics = {
    cacheHits: 0,
    cacheMisses: 0,
    requestsDeduplicated: 0,
    prefetchSuccess: 0,
    prefetchFailure: 0,
    totalRequests: 0,
    averageResponseTime: 0,
    lastResetTime: Date.now()
  };
  
  // Configuration
  private config: OptimizationConfig = {
    enableCaching: true,
    enablePrefetching: true,
    enableDeduplication: true,
    enableBatching: true,
    cacheSize: 1000,
    defaultTTL: 10 * 60 * 1000, // 10 minutes
    navigationTrackingEnabled: true,
    performanceMonitoringEnabled: true
  };

  // Cache TTL configurations for different data types
  private readonly TTL_CONFIG = {
    exercises: 60 * 60 * 1000, // 1 hour - stable data
    muscleGroups: 60 * 60 * 1000, // 1 hour - stable data
    workoutTemplates: 10 * 60 * 1000, // 10 minutes - moderate changes
    workoutLogs: 5 * 60 * 1000, // 5 minutes - frequently changing
    todaysWorkout: 2 * 60 * 1000, // 2 minutes - very dynamic
    userSettings: 30 * 60 * 1000, // 30 minutes - occasional changes
    dashboardStats: 5 * 60 * 1000, // 5 minutes - regularly updated
  };

  // Dependency mapping - when one data type changes, invalidate dependent types
  private readonly DEPENDENCY_MAP = {
    workoutLogs: ['dashboardStats', 'muscleRecovery', 'workoutHistory'],
    todaysWorkout: ['dashboardStats'],
    workoutTemplates: ['quickStartTemplates'],
    exercises: ['muscleGroups', 'workoutTemplates'],
    userSettings: ['dashboardStats', 'workoutTemplates']
  };

  private constructor() {
    this.initializeCleanupTimer();
    this.initializeNavigationTracking();
  }

  public static getInstance(): ApiOptimizationManager {
    if (!ApiOptimizationManager.instance) {
      ApiOptimizationManager.instance = new ApiOptimizationManager();
    }
    return ApiOptimizationManager.instance;
  }

  /**
   * Main method to optimize API calls - wraps existing API functions
   */
  public async optimizedFetch<T>(
    apiFunction: () => Promise<T>,
    dataType: string,
    params: any = {},
    options: {
      forceRefresh?: boolean;
      skipCache?: boolean;
      priority?: 'high' | 'medium' | 'low';
      dependencies?: string[];
    } = {}
  ): Promise<T> {
    const startTime = Date.now();
    this.metrics.totalRequests++;

    try {
      const cacheKey = this.generateCacheKey(dataType, params);
      
      // Check for existing pending request (deduplication)
      if (this.config.enableDeduplication && this.pendingRequests.has(cacheKey)) {
        this.metrics.requestsDeduplicated++;
        return this.waitForPendingRequest<T>(cacheKey);
      }

      // Check cache first
      if (!options.forceRefresh && !options.skipCache && this.config.enableCaching) {
        const cachedData = this.getCachedData<T>(cacheKey);
        if (cachedData !== null) {
          this.metrics.cacheHits++;
          this.updatePerformanceMetrics(startTime);
          return cachedData;
        }
      }

      this.metrics.cacheMisses++;

      // Create and track pending request
      const promise = this.executeApiCall(apiFunction, cacheKey, dataType, options.dependencies);
      this.pendingRequests.set(cacheKey, {
        promise,
        timestamp: Date.now(),
        subscribers: []
      });

      // Execute the API call
      const result = await promise;
      
      // Cache the result
      if (this.config.enableCaching && !options.skipCache) {
        this.setCachedData(cacheKey, result, dataType, options.dependencies);
      }

      this.updatePerformanceMetrics(startTime);
      return result;

    } catch (error) {
      this.updatePerformanceMetrics(startTime);
      
      // Try to return stale data if available
      if (this.config.enableCaching) {
        const cacheKey = this.generateCacheKey(dataType, params);
        const staleData = this.getStaleData<T>(cacheKey);
        if (staleData !== null) {
          console.warn(`API call failed, returning stale data for ${dataType}:`, error);
          return staleData;
        }
      }
      
      throw error;
    } finally {
      // Clean up pending request
      const cacheKey = this.generateCacheKey(dataType, params);
      this.pendingRequests.delete(cacheKey);
    }
  }

  /**
   * Batch multiple API calls together
   */
  public async batchFetch<T>(
    requests: Array<{
      apiFunction: () => Promise<any>;
      dataType: string;
      params?: any;
      options?: any;
    }>,
    batchKey?: string
  ): Promise<T[]> {
    if (!this.config.enableBatching) {
      return Promise.all(requests.map(req => 
        this.optimizedFetch(req.apiFunction, req.dataType, req.params, req.options)
      ));
    }

    const actualBatchKey = batchKey || `batch_${Date.now()}`;
    
    // Group requests that can be batched together
    const batchPromises = requests.map(req => 
      this.optimizedFetch(req.apiFunction, req.dataType, req.params, req.options)
    );

    return Promise.all(batchPromises);
  }

  /**
   * Prefetch data based on navigation patterns
   */
  public async prefetchForPage(targetPage: string): Promise<void> {
    if (!this.config.enablePrefetching) return;

    try {
      const pattern = this.navigationPatterns.get(`${this.currentPage}->${targetPage}`);
      
      if (pattern && pattern.dataNeeded.length > 0) {
        // Prefetch data that's commonly needed for this navigation
        const prefetchPromises = pattern.dataNeeded.map(async (dataType) => {
          try {
            await this.prefetchDataType(dataType);
            this.metrics.prefetchSuccess++;
          } catch (error) {
            console.warn(`Prefetch failed for ${dataType}:`, error);
            this.metrics.prefetchFailure++;
          }
        });

        await Promise.allSettled(prefetchPromises);
      }

      // Also prefetch based on common patterns
      await this.prefetchCommonData(targetPage);
      
    } catch (error) {
      console.warn('Prefetch operation failed:', error);
      this.metrics.prefetchFailure++;
    }
  }

  /**
   * Track page navigation for pattern learning
   */
  public trackNavigation(fromPage: string, toPage: string, dataUsed: string[] = []): void {
    if (!this.config.navigationTrackingEnabled) return;

    this.currentPage = toPage;
    this.navigationHistory.push(toPage);
    
    // Keep only last 50 navigation entries
    if (this.navigationHistory.length > 50) {
      this.navigationHistory = this.navigationHistory.slice(-50);
    }

    // Update navigation patterns
    const patternKey = `${fromPage}->${toPage}`;
    const existingPattern = this.navigationPatterns.get(patternKey);
    
    if (existingPattern) {
      existingPattern.frequency++;
      existingPattern.lastOccurrence = Date.now();
      // Merge data types used
      existingPattern.dataNeeded = [...new Set([...existingPattern.dataNeeded, ...dataUsed])];
    } else {
      this.navigationPatterns.set(patternKey, {
        fromPage,
        toPage,
        frequency: 1,
        lastOccurrence: Date.now(),
        dataNeeded: dataUsed
      });
    }

    this.lastNavigationTime = Date.now();
  }

  /**
   * Invalidate cache for specific data types
   */
  public invalidateCache(dataTypes: string | string[]): void {
    const types = Array.isArray(dataTypes) ? dataTypes : [dataTypes];
    
    types.forEach(dataType => {
      // Remove direct cache entries
      const keysToDelete: string[] = [];
      this.cache.forEach((_, key) => {
        if (key.startsWith(`${dataType}:`)) {
          keysToDelete.push(key);
        }
      });
      
      keysToDelete.forEach(key => this.cache.delete(key));
      
      // Invalidate dependent data types
      const dependencies = this.DEPENDENCY_MAP[dataType as keyof typeof this.DEPENDENCY_MAP];
      if (dependencies) {
        dependencies.forEach(depType => this.invalidateCache(depType));
      }
    });
  }

  /**
   * Get cache statistics and performance metrics
   */
  public getMetrics(): {
    cache: {
      size: number;
      hitRate: number;
      entries: Array<{ key: string; size: number; lastAccessed: number; accessCount: number }>;
    };
    performance: PerformanceMetrics;
    navigation: {
      currentPage: string;
      recentHistory: string[];
      patterns: Array<{ pattern: string; frequency: number; lastUsed: number }>;
    };
  } {
    const hitRate = this.metrics.totalRequests > 0 
      ? (this.metrics.cacheHits / (this.metrics.cacheHits + this.metrics.cacheMisses)) * 100 
      : 0;

    const cacheEntries = Array.from(this.cache.entries()).map(([key, entry]) => ({
      key,
      size: JSON.stringify(entry.data).length,
      lastAccessed: entry.lastAccessed,
      accessCount: entry.accessCount
    }));

    const navigationPatterns = Array.from(this.navigationPatterns.entries()).map(([key, pattern]) => ({
      pattern: key,
      frequency: pattern.frequency,
      lastUsed: pattern.lastOccurrence
    }));

    return {
      cache: {
        size: this.cache.size,
        hitRate: Math.round(hitRate * 100) / 100,
        entries: cacheEntries.sort((a, b) => b.accessCount - a.accessCount)
      },
      performance: { ...this.metrics },
      navigation: {
        currentPage: this.currentPage,
        recentHistory: this.navigationHistory.slice(-10),
        patterns: navigationPatterns.sort((a, b) => b.frequency - a.frequency)
      }
    };
  }

  /**
   * Configure optimization settings
   */
  public configure(newConfig: Partial<OptimizationConfig>): void {
    this.config = { ...this.config, ...newConfig };
  }

  /**
   * Clear all caches and reset metrics
   */
  public reset(): void {
    this.cache.clear();
    this.pendingRequests.clear();
    this.navigationPatterns.clear();
    this.batchQueue.clear();
    this.navigationHistory = [];
    this.metrics = {
      cacheHits: 0,
      cacheMisses: 0,
      requestsDeduplicated: 0,
      prefetchSuccess: 0,
      prefetchFailure: 0,
      totalRequests: 0,
      averageResponseTime: 0,
      lastResetTime: Date.now()
    };
  }

  // Private helper methods

  private async executeApiCall<T>(
    apiFunction: () => Promise<T>,
    cacheKey: string,
    dataType: string,
    dependencies?: string[]
  ): Promise<T> {
    try {
      const result = await apiFunction();
      return result;
    } catch (error) {
      // Remove from pending requests on error
      this.pendingRequests.delete(cacheKey);
      throw error;
    }
  }

  private async waitForPendingRequest<T>(cacheKey: string): Promise<T> {
    const pendingRequest = this.pendingRequests.get(cacheKey);
    if (pendingRequest) {
      return pendingRequest.promise;
    }
    throw new Error('Pending request not found');
  }

  private generateCacheKey(dataType: string, params: any): string {
    const paramString = params && Object.keys(params).length > 0 
      ? JSON.stringify(params) 
      : '';
    return `${dataType}:${paramString}`;
  }

  private getCachedData<T>(cacheKey: string): T | null {
    const entry = this.cache.get(cacheKey);
    if (!entry) return null;

    const now = Date.now();
    
    // Check if expired
    if (now > entry.expiresAt) {
      // Mark as stale but keep for potential fallback
      entry.stale = true;
      return null;
    }

    // Update access statistics
    entry.lastAccessed = now;
    entry.accessCount++;

    return entry.data;
  }

  private getStaleData<T>(cacheKey: string): T | null {
    const entry = this.cache.get(cacheKey);
    if (!entry) return null;

    // Return stale data regardless of expiration
    entry.lastAccessed = Date.now();
    return entry.data;
  }

  private setCachedData<T>(
    cacheKey: string, 
    data: T, 
    dataType: string, 
    dependencies?: string[]
  ): void {
    // Get TTL for this data type
    const ttl = this.TTL_CONFIG[dataType as keyof typeof this.TTL_CONFIG] || this.config.defaultTTL;
    
    // Check cache size limit
    if (this.cache.size >= this.config.cacheSize) {
      this.evictLeastUsed();
    }

    const now = Date.now();
    this.cache.set(cacheKey, {
      data,
      timestamp: now,
      expiresAt: now + ttl,
      accessCount: 1,
      lastAccessed: now,
      dependencies,
      stale: false
    });
  }

  private evictLeastUsed(): void {
    let oldestKey = '';
    let oldestTime = Date.now();

    this.cache.forEach((entry, key) => {
      if (entry.lastAccessed < oldestTime) {
        oldestTime = entry.lastAccessed;
        oldestKey = key;
      }
    });

    if (oldestKey) {
      this.cache.delete(oldestKey);
    }
  }

  private async prefetchDataType(dataType: string): Promise<void> {
    // This would be implemented to call the appropriate API function based on data type
    // For now, it's a placeholder that would integrate with existing API clients
    console.log(`Prefetching ${dataType}...`);
  }

  private async prefetchCommonData(targetPage: string): Promise<void> {
    // Define common data needs per page
    const commonDataNeeds: Record<string, string[]> = {
      'dashboard': ['exercises', 'workoutLogs', 'dashboardStats'],
      'exerciseList': ['exercises', 'muscleGroups'],
      'workoutTemplates': ['workoutTemplates', 'exercises'],
      'todaysWorkout': ['todaysWorkout', 'exercises'],
      'workoutBuilder': ['exercises', 'muscleGroups', 'workoutTemplates'],
      'activeWorkout': ['todaysWorkout', 'exercises']
    };

    const dataToPreload = commonDataNeeds[targetPage] || [];
    
    for (const dataType of dataToPreload) {
      try {
        await this.prefetchDataType(dataType);
      } catch (error) {
        console.warn(`Failed to prefetch ${dataType} for ${targetPage}:`, error);
      }
    }
  }

  private updatePerformanceMetrics(startTime: number): void {
    if (!this.config.performanceMonitoringEnabled) return;

    const responseTime = Date.now() - startTime;
    
    // Update rolling average response time
    this.metrics.averageResponseTime = 
      (this.metrics.averageResponseTime * (this.metrics.totalRequests - 1) + responseTime) / 
      this.metrics.totalRequests;
  }

  private initializeCleanupTimer(): void {
    // Clean up expired cache entries every 5 minutes
    setInterval(() => {
      const now = Date.now();
      const keysToDelete: string[] = [];

      this.cache.forEach((entry, key) => {
        if (now > entry.expiresAt + (24 * 60 * 60 * 1000)) { // Keep stale data for 24 hours
          keysToDelete.push(key);
        }
      });

      keysToDelete.forEach(key => this.cache.delete(key));
      
      if (keysToDelete.length > 0) {
        console.log(`Cleaned up ${keysToDelete.length} expired cache entries`);
      }
    }, 5 * 60 * 1000);
  }

  private initializeNavigationTracking(): void {
    // Track page visibility changes to detect navigation
    if (typeof document !== 'undefined') {
      document.addEventListener('visibilitychange', () => {
        if (!document.hidden) {
          // Page became visible - might have navigated here
          this.lastNavigationTime = Date.now();
        }
      });
    }
  }
}

export default ApiOptimizationManager;
