/**
 * Authentication validation utilities
 * Provides validation and diagnostic tools for Google Auth configuration
 */

import { googleConfig } from '@/config/google.config';

export interface ValidationResult {
  isValid: boolean;
  errors: string[];
  warnings: string[];
}

/**
 * Validates Google API configuration
 */
export function validateGoogleConfig(): ValidationResult {
  const errors: string[] = [];
  const warnings: string[] = [];

  // Check API Key (from environment variable)
  const apiKey = import.meta.env.VITE_GOOGLE_API_KEY;
  if (!apiKey || apiKey === 'your-api-key-here') {
    errors.push('Google API Key is missing or not configured. Please set VITE_GOOGLE_API_KEY environment variable.');
  } else if (apiKey.length < 30) {
    warnings.push('Google API Key appears to be too short. Please verify it is correct.');
  }

  // Check Client ID
  if (!googleConfig.auth.clientId || googleConfig.auth.clientId === 'your-client-id-here') {
    errors.push('Google Client ID is missing or not configured. Please set VITE_GOOGLE_CLIENT_ID environment variable.');
  } else if (!googleConfig.auth.clientId.includes('.googleusercontent.com')) {
    warnings.push('Google Client ID format appears incorrect. It should end with .googleusercontent.com');
  }

  // Check Spreadsheet ID
  if (!googleConfig.sheets.spreadsheetId || googleConfig.sheets.spreadsheetId === 'your-spreadsheet-id-here') {
    errors.push('Google Spreadsheet ID is missing or not configured. Please set VITE_GOOGLE_SHEET_ID environment variable.');
  } else if (googleConfig.sheets.spreadsheetId.length !== 44) {
    warnings.push('Google Spreadsheet ID length appears incorrect. Should be 44 characters long.');
  }

  // Check scopes
  if (!googleConfig.sheets.scopes || googleConfig.sheets.scopes.length === 0) {
    errors.push('Google Sheets scopes are not configured.');
  }

  return {
    isValid: errors.length === 0,
    errors,
    warnings
  };
}

/**
 * Logs configuration validation results
 */
export function logConfigValidation(): void {
  const result = validateGoogleConfig();
  
  if (!result.isValid) {
    console.error('❌ Google Configuration Validation Failed:');
    result.errors.forEach(error => console.error(`  • ${error}`));
  } else {
    console.log('✅ Google Configuration Validation Passed');
  }

  if (result.warnings.length > 0) {
    console.warn('⚠️ Google Configuration Warnings:');
    result.warnings.forEach(warning => console.warn(`  • ${warning}`));
  }
}

/**
 * Gets setup instructions for missing configuration
 */
export function getSetupInstructions(): string {
  const result = validateGoogleConfig();
  
  if (result.isValid) {
    return 'Google configuration is properly set up!';
  }

  return `
📋 Google Auth Setup Instructions:

1. Create a .env file in your project root with the following variables:
   VITE_GOOGLE_API_KEY=your-google-api-key
   VITE_GOOGLE_CLIENT_ID=your-google-client-id.googleusercontent.com
   VITE_GOOGLE_SPREADSHEET_ID=your-spreadsheet-id

2. Get your Google API credentials:
   - Go to https://console.cloud.google.com/
   - Create a new project or select existing
   - Enable Google Sheets API and Google Drive API
   - Create credentials (API Key and OAuth 2.0 Client ID)

3. Configure OAuth 2.0:
   - Set authorized origins: ${window.location.origin}
   - Set authorized redirect URIs: ${window.location.origin}

4. Create a Google Sheets document and note its ID from the URL

Current Issues:
${result.errors.map(error => `• ${error}`).join('\n')}

${result.warnings.length > 0 ? `\nWarnings:\n${result.warnings.map(warning => `• ${warning}`).join('\n')}` : ''}
`;
}

/**
 * Checks if authentication environment is ready
 */
export function isAuthEnvironmentReady(): boolean {
  return validateGoogleConfig().isValid;
}
